.container {
    float: left;
}

.card {
    -ms-flex-line-pack: center;
        align-content: center;
    background: #fff;
    border-radius: 2px;
    height: 275px;
    margin: 1rem;
    position: relative;
    /* width: 30%; */
    /* width: 275px; */
    -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
            box-shadow: 0 3px 6px #999, 0 3px 6px #999;
    text-align: left;
    float: left;
   }
   
   .card > .imgContainer {
    height: 100%;
    overflow: hidden;
    /* text-align: center; */
    /* background:#6CADDC; */
   }

   .card > .imgContainer > img {
    width: 100%;
    height: 100%;
   }
   
   .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
   }
   
   .card > .content > ul {
    list-style-type: none;
   }
   
   .card > .content > ul > li {
    margin-top: 18px;
   }


